@font-face {
  font-family: 'aukphilaregular';
  src: url('https://cdn.circuitauction.com/fonts/aukphila-webfont.eot');
  src: url('https://cdn.circuitauction.com/fonts/aukphila-webfont.eot?#iefix') format('embedded-opentype'),
  url('https://cdn.circuitauction.com/fonts/aukphila-webfont.woff') format('woff'),
  url('https://cdn.circuitauction.com/fonts/aukphila-webfont.ttf') format('truetype'),
  url('https://cdn.circuitauction.com/fonts/aukphila-webfont.svg#aukphilaregular') format('svg');
  font-weight: normal;
  font-style: normal;
}

.aukphila {
  font-family: 'aukphilaregular';
}

.user-block {

  .user-link{
    justify-content:space-between;
    margin: 0 3px;
  }

  .logged-in{
    position: relative;

    button{
      text-align: left;
      border: none;
      background: #fff;

      .fa{
        margin: 0 5px -1px 5px;
      }

      .fa-chevron-up{
        display: none;
      }
    }

    button:focus{
      outline: none;
    }

    .dropdown-menu{
      display: none;
    }

    .open {
      .dropdown-menu{
        display: flex;
        flex-direction: column;
        position: absolute;
        z-index: 1000;
        top:26px;
        background: #fff;
        padding: 10px;
        width: 150px;
        min-width: 100px;
        line-height: 1.5em;
        border-bottom: solid 1px #ECF0F5;
        border-left: solid 1px #ECF0F5;
        border-right: solid 1px #ECF0F5;
      }

      .fa-chevron-up{
        display: inline-block;
      }
    }
  }

}

.my-bids {

  .page-header {
    text-align: center;
    font-size: 32px;
  }

  .chakra-accordion__button {
    background-color: #f1f1f1;
    padding: 0.5em 2em;
  }

  .h3-heading {
    font-weight: bold;
    font-size: 16px;
  }

  .past-sale-title {
    text-align: center;
    font-size: 1.65em;
  }
  .sale-dates {
    font-size: 0.7em;
  }
  .sale-date {
    font-size: 18px;
  }
  .sale-summary :nth-child(1) {
    padding-bottom: 0;
    margin-bottom: 0;
  }
  .groupbids-summary {
    padding: 0px 16px 0;
    margin: 0 0 20px 0px;

    .group-summary-label {
      font-weight: bold;
      margin-right: 25px;
    }
    .group-info .dot {
      padding: 20px;
    }
  }

  .lot-info {
    @media only screen and (min-width: 660px) {
      display: grid;
      grid-template-columns: 1fr 3fr 2fr;
      grid-gap: 0 2em;
    }
    padding-bottom: 0.5em;
    border-bottom: solid 1px #ccc;
    margin-bottom: 0;

    .lot-number {
      @media only screen and (min-width: 660px) {
        padding: 1em 1em 0.5em;
      }
      font-size: 1.2em;      
      a {
        padding: 0 4px;
        
      }
    }    
    .lot-price {
      @media only screen and (min-width: 660px) {
        padding: 1em 0 0.5em;
      }
      font-size: 1.2em;
    }
    .current-price {
      @media only screen and (min-width: 660px) {
        padding: 1em 0 0.5em;
        text-align: center;
      }
      font-size: 1.2em;
      
    }
    .lot-image img {
      padding: 0 1em;
      margin: 0 auto;
    }
    
    .lot-image .lot-image-thumbs {
      display: none;
    }
    .chakra-table {
      margin: 0 auto;
    }

    .lot-bids{
      h4 {
        text-align: center;
      }

      table td {
        padding: 0.15em 0.8em;
      }
      .bid-type {
        font-size: 0.9em;
        font-style: italic;
        vertical-align: top;
      }
      .bid-date {
        font-size: 0.85em;
      }
      .edit-bid {
        button {
          margin-top: 0;
        }
        .chakra-icon {
          margin-left: 6px;
          margin-bottom: 5px;
        }
      }
      .bidder-id {
        text-align: center;
      }
    }
    .back-to-lot {
      display: flex;
      justify-content: center;
      margin-top: 1em;
      @media only screen and (min-width: 660px) {
        grid-column: span 3;
      }
      a {
        border: solid 1px #3182ce;
        padding: 0.5em 1em 0.3em;
        color: #3182ce;
      }
      a:hover {
        background: #3182ce;
        color: #fff;
      }

    }
  }

  .lot-table {

    td {
      padding-top: 10px;
      padding-bottom: 10px;
    }
    .bidder-number {
      font-style: italic;
    }
  }

  .won-item {
    color: green;
    font-size: 14px;
    margin-top: -6px;
  }

  .chakra-table {
    width: auto;

    .edit-btn {
      color: #3182ce;

      .chakra-icon.css-onkibi {
        font-size: 16px;
        margin-left: 4px;
        margin-bottom: 4px;
      }
    }
  }

  .chakra-alert {
    width: auto;
    display: inline-flex;
  }

  .chakra-form-control {
    @media only screen and (min-width: 660px) {
      display: flex;
    }

    .chakra-form__label {
      margin-top: 1.7em;
    }

    .chakra-input {
      width: auto;
      @media only screen and (min-width: 660px) {
        margin: 1em 1em 0;
      }
      
    }
  }

  .bid-summary {

    justify-content: center;

    p {
      margin: 0;
    }
  }

  .popup {
    position: fixed;
    top: 40%;
    left: 50%;
    transform: translate(-50%, -60%);
    background: #fff;
    padding: 1em 2em;
    border: solid 1px #ECF0F5;
    box-shadow: #4e5b61 8px 8px 6px;

    .close-button {
      color: #3182ce;
      float: right;
      font-size: 14px;

      .chakra-icon.css-onkibi {
        font-size: 12px;
        margin-left: 4px;
        margin-bottom: 4px;
      }
    }

    .clear {
      clear: both;
    }
  }

  .bottom-text {
    font-size: 16px;
    padding: 0.5em 1em
  }

  .bidderNumber {
    font-size: 18px;
    font-weight: bold;
    float: left;
    padding: 12px 22px 12px 0;
  }
  .maxbidinput {
    margin-top: 0 !important;
  }
  .line-through {
    text-decoration: line-through;
  }
  
  .deleted-label {
    color: #e53e3e;
    font-size: 0.875rem;
    margin: 0 0.5rem;
    cursor: help;
  }

  .special-bid-status {
    color: #e5833e;
    font-size: 0.875rem;
    margin: 0 0.5rem;
    cursor: help;
  }
  
}

.help-text {
  margin-top: 10px;
  margin-bottom: 20px;
  font-size: 14px;
  color: gray;
}
.red {
  color: red;;
}

.phone-number-label {
  padding-top: 12px;
}

.phone-input-dropdown {
  overflow: auto;
  @media (min-width: 768px) {
    display: flex;
  }
}

.PhoneInput {


  .PhoneInputCountrySelect {
    width: 200px;
    background: #fff;
    border: solid 1px #ECF0F5;
    border-radius: 7px;
    padding: 10px 12px;
    margin-top: 2px;
  }
  
  .PhoneInputCountryIcon {
    width: 32px;
    float: left;
    margin: 0 10px;
    padding-top: 12px
  }

  .PhoneInputInput {
    margin: 0 15px;
    border: solid 1px #ECF0F5;
    border-radius: 5px;
    padding: 6px 10px 4px;
  }
}

.react-datepicker-wrapper {
  input {
    border: solid 1px #ECF0F5;
    padding: 8px 20px 6px;
    border-radius: 5px;
  }
}

.user-login {
  @media (min-width: 768px) {
    width: 60%;
    margin: 3em auto;
    padding: 1em 2em;
    border: solid 2px #cccccc;
  }
  


  .forgot-password-link {
    display: flex;
    justify-content: end;
  }
}

.address-override {
  border: solid 1px #ccc;
}



.terms-and-conditions div {
  align-items: baseline;

  a {
    text-decoration: underline;
    color: #3182ce;
  }
}
.account {
  input, textarea {
    max-width: 98% ;
    @media (max-width: 768px) {
      max-width: 88% ;
    }
  }
}

.modal-img {
  max-width: 100%;
}

// Image style
.lot-image {
  grid-row: span 3;
  padding: 0 5px;

  @media only screen and (max-width: 1200px) {
    border: none;
  }

  .lot-image-thumbs {
    max-width: 220px;
    text-align: center;
    display: block; // Changed from inline-flex to block
    overflow-x: scroll;
    overflow-y: hidden;
    white-space: nowrap;
    -webkit-overflow-scrolling: touch;
    scrollbar-width: auto;
    scrollbar-color: #848484 transparent;
  
    &::-webkit-scrollbar {
      width: 12px;
      height: 20px;
    }
  
    &::-webkit-scrollbar-track {
      background: #cecece;
    }
  
    &::-webkit-scrollbar-thumb {
      background-color: #bdbdbd;
      border-radius: 6px;
      border: 3px solid #f1f1f1;
    }
  
    

    img {
      display: inline-block;
      margin: 1px;
      cursor: pointer;
      max-height: 108px;
      border: 2px solid transparent;
      transition: border-color 0.3s;

      &:hover {
        border-color: rgba(#bdbdbd, 0.5);
      }

    }
  }

  .images-count {
    text-align: center;
    margin-top: 8px;
    small {
      color: #666;
    }
  }

  .current-zoomed-image {
    margin: 0;
    padding: 0;
  }

  .no-image-placeholder{
    background: #ededed;
    text-align: center;
    padding: 20px;
    color: gray;
  }
}
